@mixin getChangeWidthKeyframes($initial: 0%, $final: 100%) {
  0% {
    width: $initial;
  }
  100% {
    width: $final;
  }
}

@mixin getChangeMarginLeftKeyframes($initial: 0%, $final: 100%) {
  0% {
    margin-left: $initial;
  }
  100% {
    margin-left: $final;
  }
}

@keyframes collapseMap {
  @include getChangeWidthKeyframes(100%, $mapMenuLayout);
}

@keyframes expandMap {
  @include getChangeWidthKeyframes($mapMenuLayout, 100%);
}

@keyframes collapseMenu {
  @include getChangeWidthKeyframes(100% - $mapMenuLayout, 0%);
}

@keyframes expandMenu {
  @include getChangeWidthKeyframes(0%, 100% - $mapMenuLayout);
}

@keyframes collapseMenuMargin {
  @include getChangeMarginLeftKeyframes($componentSpace, 0px);
}

@keyframes expandMenuMargin {
  @include getChangeMarginLeftKeyframes(0px, $componentSpace);
}

@media screen and (min-width: $smallScreenTreshold) and (max-width: $mediumScreenTreshold) {
  @keyframes collapseMap {
    @include getChangeWidthKeyframes(100%, $mapMenuLayoutSmall);
  }

  @keyframes expandMap {
    @include getChangeWidthKeyframes($mapMenuLayoutSmall, 100%);
  }

  @keyframes collapseMenu {
    @include getChangeWidthKeyframes($mapMenuLayoutSmall, 0%);
  }

  @keyframes expandMenu {
    @include getChangeWidthKeyframes(0%, $mapMenuLayoutSmall);
  }
}

@media screen and (max-width: $smallScreenTreshold) {
  @keyframes collapseMap {
    @include getChangeWidthKeyframes(100%, 0%);
  }

  @keyframes expandMap {
    @include getChangeWidthKeyframes(0%, 100%);
  }

  @keyframes collapseMenu {
    @include getChangeWidthKeyframes(100%, 0%);
  }

  @keyframes expandMenu {
    @include getChangeWidthKeyframes(0%, 100%);
  }
}
