@import "./../../styles/styles";

.InfoWindow {
  position: fixed;
  margin: 0px;
  padding: 0px;
  bottom: $componentSpace;
  overflow: hidden;
  width: 100% - $mapMenuLayout;
  top: $componentSpace;
  right: 0px;
  background-color: black;
  z-index: 1000;

  @media screen and (max-width: $smallScreenTreshold) {
    width: 100%;
  }

  @media screen and (min-width: $smallScreenTreshold) and (max-width: $mediumScreenTreshold) {
    width: $mapMenuLayoutSmall;
  }

  &_container {
    margin: 0px $componentSpace;
    background-color: $backgroundColor;
    height: 100%;
    border-radius: $borderRadius;
  }

  &_closeButton {
    @include resetButtonDecorations;
    position: absolute;
    top: 0px;
    right: $componentSpace;
    margin-right: 8px;
    margin-top: 4px;
  }

  &_menu {
    margin: 0px 4px;
    padding: 8px 0px;
  }

  &_menuItem {
    padding: 4px 0px;
    margin: 0px 6px;
  }

  &_menuButton {
    @include resetButtonDecorations;
    font-size: 14px;
    cursor: unset;
  }

  &_menuIcon {
    font-size: 14px;
    margin-right: 4px;
    vertical-align: middle;
  }

  &_description {
    margin: 0px 10px;
    padding: 8px 4px;
    overflow: auto;
    max-height: 400px;
  }
}
