@import "./../../styles/styles";

.Statystyki {
  margin: 0px 10px;
  padding: 8px 4px;
  overflow: hidden;

  &_header {
    font-weight: bold;
    margin-bottom: 8px;

    &--plany {
      @extend .Statystyki_header;
      padding-top: 8px;
      margin-top: 8px;
      border-top: 1px solid $colorSoftTab;
    }
  }

  &_item {
    padding: 2px 0px;
    margin: 0px;
    font-size: 14px;
  }

  &_list {
    max-height: 70px;
    width: 75%;
    overflow: auto;
    display: inline-block;
    vertical-align: top;
    margin: 0px 10px;
    padding: 0px 10px;
    list-style: inside;
    border-top: #aaa 1px solid;
    border-bottom: #aaa 1px solid;
    background-color: #eee;
  }
}
