@import "./../../styles/styles";

.InfoWindowNajczestszeTrasy {
  border-top: 1px solid $colorSoftTab;
  padding: 8px 0px;
  margin: 4px 0px;

  &_list {
    display: inline-block;
    vertical-align: top;
    margin: 0px;
    margin-left: 10px;
    padding: 0px;
    list-style: inside;
  }

  &_icon {
    font-size: 14px;
    margin-right: 4px;
    width: 14px;
    vertical-align: middle;
  }
}
