@import "./../../styles/styles";

.MapWrapper {
  position: relative;
  margin: 0px;
  display: inline-block;
  padding: 0px;
  height: 100%;
  overflow: hidden;
  width: $mapMenuLayout;

  @media screen and (min-width: $smallScreenTreshold) and (max-width: $mediumScreenTreshold) {
    width: $mapMenuLayoutSmall;
  }

  @media screen and (max-width: $smallScreenTreshold) {
    width: 100%;
  }

  &--menuShown {
    @extend .MapWrapper;
    animation: collapseMap $animationTime ease 1;

    @media screen and (max-width: $smallScreenTreshold) {
      width: 0%;
    }
  }

  &--menuHidden {
    @extend .MapWrapper;
    animation: expandMap $animationTime ease 1;
    width: 100%;
  }
}

.Map_container {
  height: 100%;
  width: 100%;
  position: relative;
}

.Map {
  border-radius: $borderRadius;
  width: 100%;
  height: 100%;
}
