@import "./../../styles/styles";

.OpisWyjazduRelacje {
  border-top: 1px solid $colorSoftTab;
  padding: 0px;
  margin: 4px 0px;

  &_list {
    display: inline-block;
    vertical-align: top;
    margin: 0px;
    margin-left: 10px;
    padding: 0px;
    list-style: inside;
    max-width: 70%;
  }

  &_icon {
    font-size: 14px;
    margin-right: 4px;
    vertical-align: middle;
  }
}
