@import "./../../styles/styles";

.Wyjazdy {
  margin: 0px 10px;

  &_wrapper {
    padding: 8px 4px;
    position: absolute;
    top: 34px;
    left: 8px;
    right: 0px;
    bottom: 24px;
  }

  &_header {
    font-weight: bold;
    margin-bottom: 8px;
  }

  &_button {
    @include resetButtonDecorations;

    &--show {
      @extend .Wyjazdy_button;
      color: $colorPositive;
    }

    &--showHidden {
      @extend .Wyjazdy_button;
      color: $colorPositiveSoft;
    }

    &--hide {
      @extend .Wyjazdy_button;
      color: $colorNegative;
    }

    &--hideHidden {
      @extend .Wyjazdy_button;
      color: $colorNegativeSoft;
    }
  }

  &_icon {
    font-size: 14px;
    margin-left: 4px;
    vertical-align: middle;
  }

  &_body {
    height: 100%;
    overflow: auto;
  }
}
