@import "./../../styles/styles";

.Warstwa {
  margin: 4px 0px;

  &_button {
    @include resetButtonDecorations;

    &:hover {
      text-decoration: underline;
    }
  }

  &_buttonIcon {
    font-size: 12px;
    vertical-align: middle;
    color: $colorPositive;
    margin-right: 4px;

    &--hidden {
      @extend .Warstwa_buttonIcon;
      color: $colorNegative;
    }
  }
}
