@import "./../../styles/styles";

.Lata {
  border-top: #aaa 1px solid;
  padding: 8px 4px;

  &_header {
    font-weight: bold;
    margin-bottom: 8px;
  }

  &_button {
    @include resetButtonDecorations;

    &--show {
      @extend .Lata_button;
      color: $colorPositive;
    }

    &--showHidden {
      @extend .Lata_button;
      color: $colorPositiveSoft;
    }

    &--hide {
      @extend .Lata_button;
      color: $colorNegative;
    }

    &--hideHidden {
      @extend .Lata_button;
      color: $colorNegativeSoft;
    }
  }

  &_icon {
    font-size: 14px;
    margin-left: 4px;
    vertical-align: middle;
  }

  &_body {
    max-height: 150px;
    overflow: auto;
  }
}
