@import "./../../styles/styles";

.MenuWrapper {
  position: relative;
  display: inline-block;
  margin: 0px;
  padding: 0px;
  height: 100%;
  overflow: hidden;
  width: 100% - $mapMenuLayout;

  @media screen and (max-width: $smallScreenTreshold) {
    width: 0%;
  }

  @media screen and (min-width: $smallScreenTreshold) and (max-width: $mediumScreenTreshold) {
    width: $mapMenuLayoutSmall;
  }

  &--menuShown {
    @extend .MenuWrapper;
    animation: expandMenu $animationTime ease 1;

    @media screen and (max-width: $smallScreenTreshold) {
      width: 100%;
    }
  }

  &--menuHidden {
    @extend .MenuWrapper;
    width: 0%;
    animation: collapseMenu $animationTime ease 1;
  }
}
