@import "./../../styles/styles";

.InfoWindowListaWyjazdow {
  border-top: 1px solid $colorSoftTab;
  padding: 8px 0px;
  margin: 4px 0px;

  &_list {
    display: inline-block;
    vertical-align: top;
    margin: 0px;
    margin-left: 10px;
    padding: 0px;
    list-style: inside;
    max-width: 70%;
  }

  &_link {
    color: $colorOpisLink;
    text-decoration: none;
    margin-left: 4px;
  }

  &_icon {
    font-size: 14px;
    width: 14px;
    vertical-align: middle;
  }
}
