@import "./../../styles/styles";

.MenuContainer {
  background-color: $backgroundColor;
  border-radius: $borderRadius;
  height: 100%;
  margin-left: $componentSpace;

  &--menuShown {
    @extend .MenuContainer;
    animation: expandMenuMargin $animationTime ease 1;
  }

  &--menuHidden {
    @extend .MenuContainer;
    animation: collapseMenuMargin $animationTime ease 1;
    margin-left: 0px;
  }
}

@media screen and (max-width: $smallScreenTreshold) {
  .MenuContainer,
  .MenuContainer--menuShown {
    margin-left: 0%;
  }
}
