@import "./../../styles/styles";

.MenuItem {
  $fontSize: 14px;

  padding: 4px 0px;
  margin: 0px 6px;

  &--selectedMenu {
    @extend .MenuItem;
    border-bottom: 1px solid;
  }

  &_button {
    @include resetButtonDecorations;
    font-size: $fontSize;
  }

  &_icon {
    font-size: $fontSize;
    margin-right: 4px;
    vertical-align: middle;
  }
}
